import { messageConstants } from '../constants';

export function messages(state = {}, action) {
  switch (action.type) {
    case messageConstants.GETALL_REQUEST:
      return {
        loading: true
      };
    case messageConstants.GETALL_SUCCESS:
      return {
        loading: false,
        items: action.message
      };
    case messageConstants.GETALL_FAILURE:
      return { 
        loading: false,
        error: action.error
      };
    case messageConstants.GET_GROUP_MSG_REQUEST:
      return {
        loading: true
      };
    case messageConstants.GET_GROUP_MSG_SUCCESS:
      return {
        loading: false,
        items: action.message
      };
    case messageConstants.GET_GROUP_MSG_FAILURE:
      return { 
        loading: false,
        error: action.error
      };
    case messageConstants.CREATE_REQUEST:
      return {
        loading: true
      };
    case messageConstants.CREATE_SUCCESS:
      return {
        loading: false,
        items: [...state.items, action.message]
      };
    case messageConstants.CREATE_FAILURE:
      return { 
        loading: false,
        error: action.error
      };
      case messageConstants.GETBYUSER_REQUEST:
      return {
        loading: true
      };
    case messageConstants.GETBYUSER_SUCCESS:
      return {
        loading: false,
        items: action.message
      };
    case messageConstants.GETBYUSER_FAILURE:
      return { 
        loading: false,
        error: action.error
      };
    case messageConstants.DELETE_REQUEST:
      // add 'deleting:true' property to message being deleted
      return {
        loading: true,
        ...state,
        items: state.items.map(message =>
          message.id === action.id
            ? { ...message, deleting: true }
            : message
        )
      };
    case messageConstants.DELETE_SUCCESS:
      // remove deleted message from state
      return {
        loading: false,
        items: state.items.filter(message => message.id !== action.id)
      };
    case messageConstants.DELETE_FAILURE:
      // remove 'deleting:true' property and add 'deleteError:[error]' property to message 
      return {
        loading: false,
        ...state,
        items: state.items.map(message => {
          if (message.id === action.id) {
            // make copy of message without 'deleting:true' property
            const { deleting, ...messageCopy } = message;
            // return copy of message with 'deleteError:[error]' property
            return { ...messageCopy, deleteError: action.error };
          }

          return message;
        })
      };
    default:
      return state
  }
}


