import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { Button, Form, Grid, Header, Image, Message, Segment } from 'semantic-ui-react'

import { userActions } from '../../actions';

class Register extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            user: {
                firstName: '',
                lastName: '',
                username: '',
                password: ''
            },
            submitted: false
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(event) {
        const { name, value } = event.target;
        const { user } = this.state;
        this.setState({
            user: {
                ...user,
                [name]: value
            }
        });
    }

    handleSubmit(event) {
        event.preventDefault();

        this.setState({ submitted: true });
        const { user } = this.state;
        const { dispatch } = this.props;
        if (user.firstName && user.lastName && user.username && user.password) {
            dispatch(userActions.register(user));
        }
    }

    render() {
        const { registering  } = this.props;
        const { user, submitted } = this.state;
        return (
            <Grid textAlign='center' style={{ height: '100%' }} verticalAlign='middle'>
          <Grid.Column style={{ maxWidth: 450 }}>
            <Image size='medium' centered src='/images/cix.png' />
            <Form size='large' onSubmit={this.handleSubmit}>
              <Segment>
                <Form.Input fluid icon='user' iconPosition='left' placeholder='First Name' name="firstName" value={user.firstName} onChange={this.handleChange} />
                {submitted && !user.firstName &&
                  <div className="help-block">first Name is required</div>
                }
                <Form.Input fluid icon='user' iconPosition='left' placeholder='Last Name' name="lastName" value={user.lastName} onChange={this.handleChange} />
                {submitted && !user.lastName &&
                  <div className="help-block">Last Name is required</div>
                }
                <Form.Input fluid icon='user' iconPosition='left' placeholder='Username' name="username" value={user.username} onChange={this.handleChange} />
                {submitted && !user.username &&
                  <div className="help-block">Username is required</div>
                }
                <Form.Input
                  fluid
                  icon='lock'
                  iconPosition='left'
                  placeholder='Password'
                  type='password'
                  name="password"
                  value={user.password}
                  onChange={this.handleChange}
                />
                {submitted && !user.password &&
                  <div className="help-block">Password is required</div>
                }
    
                <Button color='blue' fluid size='large'>
                  Register
                </Button>
                {registering &&
                  <img src="data:image/gif;base64," />
                }
              </Segment>
            </Form>
          </Grid.Column>
        </Grid>
        );
    }
}

function mapStateToProps(state) {
    const { registering } = state.registration;
    return {
        registering
    };
}

export default connect(mapStateToProps)(Register)



