import React, { Component } from 'react'
import {
  Divider,
  Icon,
  Image,
  Menu,
  Segment,
  Step,
  Container
} from 'semantic-ui-react'
import 'styling/semantic.less'

import { history } from '../../helpers';

class LeftSidebar extends Component {
  render() {
    let pathname = location.pathname;
    return (
    <Step.Group vertical>
        {/* <Step active={pathname == '/compose' ? true : false} onClick={() => { history.push('/compose') }}>
            <Icon name='envelope open outline' />
            <Step.Content>
            <Step.Title>Compose New</Step.Title>
            </Step.Content>
        </Step> */}
        <Step active={pathname == '/dashboard' ? true : false} >
            <Icon name='chart bar' />
            <Step.Content>
            <Step.Title><a href="/trends.html">Dashboard</a></Step.Title>
            </Step.Content>
        </Step>
        <Step active={pathname == '/' ? true : false} onClick={() => { history.push('/') }}>
            <Icon name='file alternate' />
            <Step.Content>
            <Step.Title>Communications</Step.Title>
            </Step.Content>
        </Step>
        
        <Step>
            <Icon name='paperclip' />
            <Step.Content>
            <Step.Title>Flagged Items</Step.Title>
            </Step.Content>
        </Step>
    </Step.Group>
    )

    }
}
export default LeftSidebar


