import PropTypes from 'prop-types'
import React, { Component } from 'react'
import { Router, Switch, Route, Redirect } from 'react-router-dom';

import { history } from './helpers'
import EmptyLayout from './components/layouts/EmptyLayout'
import HomepageLayout from './components/layouts/HomepageLayout'
import EmailLayout from './components/layouts/EmailLayout'
import CommunicationLayout from './components/layouts/CommunicationLayout'
import StaticLayout from './components/layouts/StaticLayout'

import CommunicationList from './components/CommunicationList'
import Login from './components/Login'
import Register from './components/Register'
import MailCompose from './components/MailCompose'
import Dashboard from './components/Dashboard'
import EmailList from './components/EmailList'
import MailChain  from './components/MailChain'
import MailCreate from './components/MailCreate'
import ComplaintsList from './components/ComplaintsList/ComplaintsList';
///
// static
import MailChainOne from './components/layouts/MailChainOne';
import MailChainTwo from './components/layouts/MailChainTwo';
import ComplianceDashboard from './components/layouts/ComplianceDashboard';
import ComplianceIssue from './components/layouts/ComplianceIssue';
import InboxView from './components/layouts/InboxView';
import MailChainThree from './components/layouts/MailChainThree';
import MailOne from './components/layouts/MailOne';
// add router

const HomepageRoute = ({component: Component, ...rest}) => {
  let user=localStorage.getItem('user');
  return (
    <Route {...rest} render={matchProps => (
      user ? 
      <HomepageLayout>
          <Component {...matchProps}/>
      </HomepageLayout>
      : 
      <Redirect to={{ pathname: '/signin', state: { from: matchProps.location } }} />
    )} />
  )
};


const PublicRoute = ({component: Component, ...rest}) => {
  return (
    <Route {...rest} render={matchProps => (
      <EmptyLayout>
          <Component {...matchProps} />
      </EmptyLayout>
    )} />
  )
};


const EmailRoute = ({component: Component, ...rest}) => {
  return (
    <Route {...rest} render={matchProps => (
      <EmailLayout>
          <Component {...matchProps} />
      </EmailLayout>
    )} />
  )
};



const CommunicationRoute = ({component: Component, ...rest}) => {
  return (
    <Route {...rest} render={matchProps => (
      <CommunicationLayout>
          <Component {...matchProps} />
      </CommunicationLayout>
    )} />
  )
};

const StaticRoute = ({component: Component, ...rest}) => {
  return (
    <Route {...rest} render={matchProps => (
      <StaticLayout>
          <Component {...matchProps} />
      </StaticLayout>
    )} />
  )
};
const App = () => (
    <Router history={history}>
      <Switch>
        <HomepageRoute exact path="/" component={CommunicationList} />
        <HomepageRoute exact path="/compose" component={MailCompose} />
        <CommunicationRoute exact path="/dashboard" component={Dashboard} />
        <CommunicationRoute exact path="/complaints" component={ComplaintsList} />
        <HomepageRoute exact path="/compose/:id" component={MailCompose} />
        <EmailRoute exact path="/inbox" component={EmailList} />
        <EmailRoute exact path="/mails/:id" component={MailChain} />
        <EmailRoute exact path="/new-mail" component={MailCreate} />
        <PublicRoute path="/signin" component={Login} />
        <PublicRoute path="/register" component={Register} />
        <StaticRoute path="/mail" component={MailChainOne} />
        <StaticRoute path="/mail-new" component={MailChainOne} />
        <StaticRoute path="/mail-issue" component={MailChainOne} />
        <StaticRoute path="/mail-view" component={MailChainTwo} />
        <StaticRoute path="/compliance-dashboard" component={ComplianceDashboard} />
        <StaticRoute path="/issue-details" component={ComplianceDashboard} />
        <StaticRoute path="/mail-view-reply" component={MailChainThree} />
        <StaticRoute path="/mail-cix" component={ComplianceIssue} />
        <StaticRoute path="/mail-home" component={MailOne} />
        <StaticRoute path="/mail-first" component={MailOne} />
        <StaticRoute path="/mail-later" component={MailOne} />
        <StaticRoute path="/inbox-view" component={InboxView} />
        <StaticRoute path="/inbox-cix" component={ComplianceIssue} />
      </Switch>
    </Router>
)


export default App


