import { userConstants } from '../constants';

export function registration(state = {}, action) {
  switch (action.type) {
    case userConstants.REGISTER_REQUEST:
      return { 
        loading: true,
        registering: true
      };
    case userConstants.REGISTER_SUCCESS:
      return {
        loading: false
      };
    case userConstants.REGISTER_FAILURE:
      return {
        loading: false
      };
    default:
      return state
  }
}


