import PropTypes from 'prop-types'
import React from 'react'
import { connect } from 'react-redux'
import { Icon,Table,Header,Image,Checkbox, Segment, Grid, Divider, Dimmer, Loader, Message  } from 'semantic-ui-react'

import { history } from '../../helpers';
import * as styles from './Dashboard.less'


class Dashboard extends React.Component {
  constructor(props) {
    super(props);
    this.user = props.users ? props.users.user : null
    
    this.state = {
      submitted: false
    };
    
  }

  render() {
    return (
    <div>
      <Dimmer active inverted>
        <Loader inverted/>
      </Dimmer>
    </div>
    );
  }

}

//maps store saved state to pros
const mapStateToProps = (state) => {
	return{
		...state
	}
}

export default connect (mapStateToProps)(Dashboard)



