import config from 'config'
import React, { Component } from 'react'
import { Form, Segment, Grid, Divider, Dimmer, Loader, Message, Button, Comment, Input } from 'semantic-ui-react'
import { connect } from 'react-redux'
import openSocket from 'socket.io-client';

import ProgressBar from '../ProgressBar'
import './MailCompose.less';
import { messageActions } from '../../actions'

const  socket = openSocket(config.socketUrl);

class MailCompose extends Component {
  constructor(props) {
    super(props);
    this.groupId = props.match.params.id | null;
    this.user = props.users ? props.users.user : null
    let policies = this.user ? this.user.policies : []
    this.props.dispatch(messageActions.getMessageGroup(this.user.username,this.groupId));
    this.props.dispatch(messageActions.getPolicies());
    this.state = {
      submitted: false
    };
    
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.renderMessages = this.renderMessages.bind(this);
    this.handleMessageValidation = this.handleMessageValidation.bind(this);
    this.subscribeToEmail((err, percent) => this.setState({ 
      percent 
    }));
  }

  subscribeToEmail(cb) {
    socket.on('checkEmail', percent => cb(null, percent));
  }
  
  renderMessages() {
    let _this = this;
    //if(!this.groupId) {this.props.messages.items = []}
    return this.props.messages.items ? 
    this.props.messages.items.map((message,key) => 
    // <Message info>
    //   <Message.Header>{message.username}</Message.Header>
    //   <p>{message.message}</p>
    // </Message>
    <Comment>
      <Comment.Avatar as='a' src={message.username == _this.user.username ? '/images/avatar_me.png' : '/images/avatar_you.png'} />
      <Comment.Content>
        <Comment.Author>{message.username}</Comment.Author>
        <Comment.Text>
          {/* <p dangerouslySetInnerHTML={{__html: message.message}}></p> */}
          <p>{message.message}</p>
        </Comment.Text>
      </Comment.Content>
    </Comment>
    )
    :
      ''
  }

  handleChange(e) {
      const { name, value } = e.target;
      this.setState({ [name]: value });
      if(name == 'message') {
        this.handleMessageValidation(e.target)
        // sample check
        console.log(value)
        //if(this.user.policies) {
        
        // }
      }
  }

  handleMessageValidation(target) {
    const { name, value } = target;
    this.setState({percent:50})
    // socket.emit('subscribeToEmail', { [name]: value });
  }

  handleSubmit(e) {
      e.preventDefault();

      this.setState({ submitted: true });
      const { message,to,subject } = this.state;
      const { dispatch } = this.props;
      let groupId = this.props.match.params.id | 0;
      let username = this.user.username;
      let payload = {user, to, subject, message, groupId}
      // if (username && message) {
      //     dispatch(messageActions.postMessageData(payload));
      //     // clear messageBox
      //     messageBox.value = ''; 
      // }
  }

  setPercentage () {
    return  Math.floor((Math.random() * 99) + 1)
  }

  render() {
    return (
      <Segment
    //style={{ padding: '3em 0em' }}
    vertical>
      <Grid container stackable verticalAlign='middle'>
        <Grid.Row>
          <Grid.Column width={16}>
          <Comment.Group>
            {this.renderMessages()}
            {this.user && this.user.role != 'insurer' ? <ProgressBar percent={this.state.percent}/> : ''}
            <Form onSubmit={this.handleSubmit}>
            <Form.Field>
              <label>To</label>
              <Input placeholder='To Address' name='to' onChange={this.handleChange}/>
            </Form.Field>
            <Form.Field>
              <label>Subject</label>
              <Input placeholder='Subject' name='subject' onChange={this.handleChange} />
            </Form.Field>
            <Form.TextArea label='Message' placeholder='Type a message' name='message' onChange={this.handleChange} />
            <Form.Button floated='right' color='blue'>Send</Form.Button>
          </Form>
          </Comment.Group>
          </Grid.Column>
        </Grid.Row>
      </Grid>
    </Segment>
    )
  }
}

//maps store saved state to pros
const mapStateToProps = (state) => {
	return{
    ...state.messages.loading,
		...state
	}
}


export default connect(mapStateToProps)(MailCompose);



