import PropTypes from 'prop-types'
import React, { Component } from 'react'
import { connect } from 'react-redux'
import {
  Container,
  Grid,
  Responsive,
  Segment,
  Sidebar,
  Menu,
  Icon,
  Modal,
  Button,
  Header
} from 'semantic-ui-react'
import 'styling/semantic.less'

import { history } from '../../helpers';
import { userActions } from '../../actions'
import MailHeader from '../MailHeader'
import LeftSidebar from '../LeftSidebar'

class EmailLayout extends Component {
  constructor(props) {
    super(props);
    this.props.dispatch(userActions.currentUser());
    this.state = {}
  }
  render() {
    const { children } = this.props
    return (
    //<Responsive minWidth={Responsive.onlyTablet.minWidth}>
    <Segment style={{ padding: '0em' }} vertical>
      <Grid container stackable verticalAlign='middle'>
        <Grid.Row>
        
        <Grid.Column floated='left' width={2}>
          <Sidebar as={Menu} icon='labeled' vertical visible width='thin'>
            <Menu.Item as='a' onClick={() => { history.push('/new-mail') }}>
                <Icon name='compose' />
                Create
            </Menu.Item>
            <Menu.Item as='a' onClick={() => { history.push('/inbox') }}>
                <Icon name='inbox' />
                Inbox
            </Menu.Item>
            <Menu.Item as='a'>
                <Icon name='send' />
                Sent
            </Menu.Item>
            <Menu.Item as='a'>
                <Icon name='firstdraft' />
                Drafts
            </Menu.Item>
          </Sidebar>
        </Grid.Column>
        <Grid.Column floated='left' width={14}>
        <MailHeader/>
            {children}
        </Grid.Column>
        </Grid.Row>
      </Grid>
    </Segment>
  //</Responsive>
    )
  }
}

EmailLayout.propTypes = {
  children: PropTypes.node,
}

//maps store saved state to pros
const mapStateToProps = (state) => {
	return{
    ...state.users.loading,
		...state.users
	}
}
export default connect(mapStateToProps)(EmailLayout)


