import PropTypes from 'prop-types'
import React, { Component } from 'react'
import {
  Grid,
  Responsive,
  Segment,
  Button,
  Image, Form, Input
} from 'semantic-ui-react'
import 'styling/semantic.less'

import { history } from '../../helpers';

class MailChainTwo extends Component {
  goToNext() {
    history.push('/mail-view-reply')
  }
  render() {
    const { children } = this.props
    let compose = '/images/mailchain2.png'
    let textBoxStyle = {
      position: 'absolute',
      top: '178px',
      left: '15px',
      width: '98%',
      height: '320px'
    }
    let toStyle = {
      position: 'absolute',
      left: '87px',
      top: '115px',
      height: '20px',
      width: '90.4%'
    }
    let ccStyle = {
      position: 'absolute',
      left: '87px',
      top: '137px',
      height: '20px',
      width: '90.4%'
    }
    let subjectStyle = {
      position: 'absolute',
      left: '87px',
      top: '159px',
      height: '20px',
      width: '90.4%'
    }
    let btnStyle ={
      background: 'none',
      position: 'absolute',
      left: '80px',
      top: '42px',
      height: '30px',
      cursor: 'pointer'
    }

    let msgValue = '<p>tttt</p><br/>thanks'
    let toValue = ''
    let subjectValue = 'Hello tes st  '

    return (
      <Segment
        textAlign='left'
        style={{ padding: '0em'}}
        vertical
        >
      <Image src={compose} verticalAlign='middle' style={{position: 'absolute',left: 0}}/>
      <Form onSubmit={this.handleSubmit}>
            {/* <Form.Field>
              <label>To</label>
              <Input placeholder='To Address' name='to' onChange={this.handleChange}/>
            </Form.Field>
            <Form.Field>
              <label>Subject</label>
              <Input placeholder='Subject' name='subject' onChange={this.handleChange} />
            </Form.Field> */}
            {/* <Input placeholder='' name='to' onChange={this.handleChange} style={toStyle}/>
            <Input placeholder='' name='cc' onChange={this.handleChange} style={ccStyle}/>
            <Input placeholder='' name='cc' onChange={this.handleChange} style={subjectStyle}/> */}
            {/* <div contenteditable="true" style={textBoxStyle} dangerouslySetInnerHTML={{__html: msgValue}}></div> */}
            <Button style={btnStyle} onClick={()=>this.goToNext()}></Button>
            {/* <Form.Button floated='right' color='blue'>Send</Form.Button> */}
          </Form>
      </Segment>
    )
  }
}

MailChainTwo.propTypes = {
  children: PropTypes.node,
}

export default MailChainTwo


