import PropTypes from 'prop-types'
import React, { Component } from 'react'
import {
  Grid,
  Responsive,
  Segment,
  Button,
  Image, Form, Input
} from 'semantic-ui-react'
import 'styling/semantic.less'

import { history } from '../../helpers';

class MailChainThree extends Component {
  goToNext() {
    history.push('/signin')
  }
  goToDetail() {
    history.push('/issue-details')
  }
  render() {
    const { children } = this.props
    let path = location.pathname
    let compose = path == '/compliance-dashboard' ? '/images/compliance.png' : '/images/detail.png'
    let top = path == '/compliance-dashboard' ? '357px' : '450px'
    let detailBtnStyle ={
      background: 'none',
      position: 'absolute',
      left: '61px',
      top: '195px',
      width: '58%'
    }
    let btnStyle ={
    //   background: 'none',
      position: 'absolute',
      left: '50px',
      top: top,
    //   height: '30px'
    }

    let msgValue = ''
    let toValue = ''
    let subjectValue = 'Hello tes st  '

    return (
      <Segment
        textAlign='center'
        style={{ padding: '0em'}}
        vertical
        >
      <Image src={compose} verticalAlign='middle' style={{position: 'absolute',left: 0}}/>
      <Form onSubmit={this.handleSubmit}>
            {/* <Form.Field>
              <label>To</label>
              <Input placeholder='To Address' name='to' onChange={this.handleChange}/>
            </Form.Field>
            <Form.Field>
              <label>Subject</label>
              <Input placeholder='Subject' name='subject' onChange={this.handleChange} />
            </Form.Field> */}
            {/* <Input placeholder='' name='to' onChange={this.handleChange} style={toStyle}/>
            <Input placeholder='' name='cc' onChange={this.handleChange} style={ccStyle}/>
            <Input placeholder='' name='cc' onChange={this.handleChange} style={subjectStyle}/> */}
            {/* <div contenteditable="true" style={textBoxStyle} dangerouslySetInnerHTML={{__html: msgValue}}></div> */}
            <Button style={btnStyle} onClick={()=>this.goToNext()}>Sign Out</Button>
            <Button style={detailBtnStyle} onClick={()=>this.goToDetail()}></Button>
            {/* <Form.Button floated='right' color='blue'>Send</Form.Button> */}
          </Form>
      </Segment>
    )
  }
}

MailChainThree.propTypes = {
  children: PropTypes.node,
}

export default MailChainThree


