import PropTypes from 'prop-types'
import React from 'react'
import { connect } from 'react-redux'
import { Icon,Table,Header,Image,Checkbox, Segment, Grid, Divider, Dimmer, Loader, Message  } from 'semantic-ui-react'

import { history } from '../../helpers';
import { messageActions } from '../../actions'
import * as styles from './CommunicationList.less'

String.prototype.trunc =
  function( n, useWordBoundary ){
      if (this.length <= n) { return this; }
      var subString = this.substr(0, n-1);
      return (useWordBoundary 
        ? subString.substr(0, subString.lastIndexOf(' ')) 
        : subString) + " ...";
  };

class CommunicationList extends React.Component {
  constructor(props) {
    super(props);
    let groupId = props.match.params.id | null;
    this.user = props.users ? props.users.user : null
    switch(this.user.role) {
      case 'insurer':
        this.props.dispatch(messageActions.getAllMessages(this.user.username));
      break;

      case 'user':
        this.props.dispatch(messageActions.getMessages(this.user.username));
      break;

      default:
        this.props.dispatch(messageActions.getMessages(this.user.username));
    }
    
    this.state = {
      submitted: false
    };
    
    this.handleClick = this.handleClick.bind(this);
    this.renderRows = this.renderRows.bind(this);
    this.getCriticality = this.getCriticality.bind(this);
  }

  handleClick(message) {

    console.log(`compose/${message.groupId}`)
    //history.push(`compose/${message.groupId}`)
  }

  getCriticality(value) {
    switch(value) {
      case 0:
        return 'grey'
      case 1:
        return 'yellow'
      case 2:
        return 'red'
      default:
        return 'grey'
    }
  }

  renderRows() {
    // Bug in semantic package :(
    // onClick={this.handleClick(message)}
    return this.props.messages.items ? 
    this.props.messages.items.map((message,key) => 
    <Table.Row onClick={() => { history.push(`/compose/${message._id}`) }}>
        <Table.Cell>
          <strong>{message.user}</strong>
        </Table.Cell>
        <Table.Cell><p>{message.title ? message.title.trunc(120,true) : ''}</p></Table.Cell>
        <Table.Cell><p>{message.flagTitle}</p></Table.Cell>
        <Table.Cell><Icon name='stop' color={this.getCriticality(message.criticalitySummary)} /></Table.Cell>
      </Table.Row>
    )
    :
      <Dimmer active inverted>
        <Loader inverted/>
      </Dimmer>
  }
  render() {
    return (
    <div>
      { (this.props.loading) ? 
      <Dimmer active inverted>
        <Loader inverted/>
      </Dimmer> : '' }
      <Table basic='very' celled collapsing>
        <Table.Header>
          <Table.Row>
            <Table.HeaderCell>User</Table.HeaderCell>
            <Table.HeaderCell>Message</Table.HeaderCell>
            <Table.HeaderCell>Flag Type</Table.HeaderCell>
            <Table.HeaderCell>Criticality</Table.HeaderCell>
          </Table.Row>
        </Table.Header>

        <Table.Body>
          {this.renderRows()}
        </Table.Body>
      </Table>
    </div>
    );
  }

}

//maps store saved state to pros
const mapStateToProps = (state) => {
	return{
    ...state.messages.loading,
		...state
	}
}

export default connect (mapStateToProps)(CommunicationList)



