import React, { Component } from 'react'
import {
  Divider,
  Icon,
  Image,
  Menu,
  Segment,
  Container,
  Header,
  Dropdown, 
  Search
} from 'semantic-ui-react'
import 'styling/semantic.less'

import './TopMenubar.less';

class TopMenubar extends Component {
    componentDidMount() {
        
    }
  render() {
    const cixLogo = '/images/cix.png'
    const {user} = this.props
    const fullName = user ? `${user.firstName} ${user.lastName}` : ''

    return (
    <Segment
        textAlign='center'
        style={{ padding: '0em'}}
        vertical
        >
        <Menu
            pointing={true}
            secondary={false}
            size='large'
        >
            <Container>
            <Menu.Item style={{border:'none'}}>
            <Image src={cixLogo} size='small' verticalAlign='middle' />
            </Menu.Item>
            <Menu.Item style={{border:'none'}}>
            <Search size='medium' className='topSearch' verticalAlign='middle'
                // loading={isLoading}
                // onResultSelect={this.handleResultSelect}
                // onSearchChange={_.debounce(this.handleSearchChange, 500, { leading: true })}
                // results={results}
                // value={value}
                // {...this.props}
            />
            </Menu.Item>
            <Menu.Item position='right' style={{border:'none'}}>
                <Dropdown text={fullName} pointing className='link item'>
                  <Dropdown.Menu>
                    <Dropdown.Item href='/signin' link={true}>Sign Out</Dropdown.Item>
                  </Dropdown.Menu>
                </Dropdown>
            </Menu.Item>
            </Container>
        </Menu>
    </Segment>
    )
  }
}

export default TopMenubar


