import PropTypes from 'prop-types'
import React, { Component } from 'react'
import {
  Grid,
  Responsive,
  Segment,
  Button,
  Image, Form, Input
} from 'semantic-ui-react'
import 'styling/semantic.less'

import { history } from '../../helpers';

class ComplianceIssue extends Component {
  goToNext() {
    history.push('/compliance-dashboard')
  }
  render() {
    const { children } = this.props
    let pathname = location.pathname
    let compose = pathname == '/mail-cix' ? '/images/cix_issue.png' : '/images/issue3.png'
    let logo = '/images/cix-logo.png'
    let left = pathname == '/mail-cix' ? '72%' : '73%' 
    let logoStyle ={
        position: 'absolute',
        left: left,
        width: '30px',
        top: '45px',
        cursor: 'pointer',
    }
    let textBoxStyle = {
      position: 'absolute',
      top: '190px',
      left: '15px',
      width: '98%',
      height: '320px',
      outline: 'none'
    }
    let toStyle = {
      position: 'absolute',
      left: '87px',
      top: '115px',
      height: '20px',
      width: '90.4%'
    }
    let ccStyle = {
      position: 'absolute',
      left: '87px',
      top: '137px',
      height: '20px',
      width: '90.4%'
    }
    let subjectStyle = {
      position: 'absolute',
      left: '87px',
      top: '159px',
      height: '20px',
      width: '90.4%'
    }
    let btnStyle ={
      background: 'none',
      position: 'absolute',
      left: '80px',
      top: '42px',
      cursor: 'pointer',
      height: '30px'
    }

    let msgValue = ''
    let toValue = ''
    let subjectValue = 'Hello tes st  '

    return (
      <Segment
        textAlign='center'
        style={{ padding: '0em'}}
        vertical
        >
      <Image src={compose} verticalAlign='middle' style={{position: 'absolute',left: 0}}/>
      <Image src={logo} size='tiny' verticalAlign='middle' style={logoStyle} onClick={()=>this.goToNext()}/>
      <Form onSubmit={this.handleSubmit}>
            {/* <Form.Field>
              <label>To</label>
              <Input placeholder='To Address' name='to' onChange={this.handleChange}/>
            </Form.Field>
            <Form.Field>
              <label>Subject</label>
              <Input placeholder='Subject' name='subject' onChange={this.handleChange} />
            </Form.Field> */}
            {/* <Input placeholder='' name='to' onChange={this.handleChange} style={toStyle}/>
            <Input placeholder='' name='cc' onChange={this.handleChange} style={ccStyle}/>
            <Input placeholder='' name='cc' onChange={this.handleChange} style={subjectStyle}/> */}
            {/* <div contenteditable="true" style={textBoxStyle} dangerouslySetInnerHTML={{__html: msgValue}}></div> */}
            <Button style={btnStyle} ></Button>
            {/* <Form.Button floated='right' color='blue'>Send</Form.Button> */}
          </Form>
      </Segment>
    )
  }
}

ComplianceIssue.propTypes = {
  children: PropTypes.node,
}

export default ComplianceIssue


