import PropTypes from 'prop-types'
import React, { Component } from 'react'
import {
  Grid,
  Responsive,
  Segment,
  Button,
  Image, Form, Input
} from 'semantic-ui-react'
import 'styling/semantic.less'

import { history } from '../../helpers';

class MailOne extends Component {
  goToNext() {
    let path = location.pathname
    if(path == '/mail-home') {
      history.push('/mail')
    } else if(path == '/mail-first') {
      history.push('/mail-new')
    } else {
      history.push('/mail-issue')
    }
  }
  render() {
    const { children } = this.props
    let compose = '/images/mail-home.png'
    let btnStyle ={
      background: 'none',
      position: 'absolute',
      left: '0px',
      top: '42px',
      height: '30px',
      cursor: 'pointer'
    }

    let msgValue = '<p>tttt</p><br/>thanks'
    let toValue = ''
    let subjectValue = 'Hello tes st  '

    return (
      <Segment
        textAlign='left'
        style={{ padding: '0em'}}
        vertical
        >
      <Image src={compose} verticalAlign='middle' style={{position: 'absolute',left: 0}}/>
      <Form onSubmit={this.handleSubmit}>
            <Button style={btnStyle} onClick={()=>this.goToNext()}></Button>
          </Form>
      </Segment>
    )
  }
}

MailOne.propTypes = {
  children: PropTypes.node,
}

export default MailOne


