import { messageConstants } from '../constants';
import { messageService } from '../services';
import { history } from '../helpers';

export const messageActions = {
    getMessages,
    getAllMessages,
    getPolicies,
    getAllComplaints,
    postMessageData,
    getMessageGroup,
    delete: _delete
};

function getMessages(username) {
    return dispatch => {
        //dispatch(request({ username }));

        messageService.getByUsername(username)
            .then(
                data => dispatch(success(data)),
                error => {
                    dispatch(failure(error.toString()));
                }
            );
    };

    function request(message) { return { type: messageConstants.GETBYUSER_REQUEST, message } }
    function success(message) { return { type: messageConstants.GETBYUSER_SUCCESS, message } }
    function failure(error) { return { type: messageConstants.GETBYUSER_FAILURE, error } }
}

function getPolicies() {
    return dispatch => {
        //dispatch(request({ username }));

        messageService.getPolicies()
            .then(
                data => dispatch(success(data)),
                error => {
                    dispatch(failure(error.toString()));
                }
            );
    };
    function success(policies) { return { type: messageConstants.GETPOLICIES_SUCCESS, policies } }
    function failure(error) { return { type: messageConstants.GETPOLICIES_FAILURE, error } }
}

function getAllComplaints() {
    return dispatch => {
        //dispatch(request({ username }));

        messageService.getComplaints()
            .then(
                data => dispatch(success(data)),
                error => {
                    dispatch(failure(error.toString()));
                }
            );
    };
    function success(complaints) { return { type: messageConstants.GETCOMPLAINTS_SUCCESS, complaints } }
    function failure(error) { return { type: messageConstants.GETCOMPLAINTS_FAILURE, error } }
}

function getAllMessages(username) {
    return dispatch => {
        //dispatch(request({ username }));

        messageService.getAll(username)
            .then(
                data => dispatch(success(data)),
                error => {
                    dispatch(failure(error.toString()));
                }
            );
    };

    function request(message) { return { type: messageConstants.GETALL_REQUEST, message } }
    function success(message) { return { type: messageConstants.GETALL_SUCCESS, message } }
    function failure(error) { return { type: messageConstants.GETALL_FAILURE, error } }
}


function getMessageGroup(username,msgId) {
    return dispatch => {
        dispatch(request({ username,msgId }));

        messageService.getMessageGroup(username,msgId)
            .then(
                data => dispatch(success(data)),
                error => {
                    dispatch(failure(error.toString()));
                }
            );
    };

    function request(username,message) { return { type: messageConstants.GET_GROUP_MSG_REQUEST, message } }
    function success(message) { return { type: messageConstants.GET_GROUP_MSG_SUCCESS, message } }
    function failure(error) { return { type: messageConstants.GET_GROUP_MSG_FAILURE, error } }
}

function postMessageData(payload) {
    return dispatch => {
        messageService.postMessageData(payload)
            .then(
                data => {
                    console.log(payload)
                    dispatch(success(data))
                    //history.push(`/compose/${data.groupId}`);
                },
                error => {
                    dispatch(failure(error.toString()));
                }
            );
    };
    function success(message) { return { type: messageConstants.CREATE_SUCCESS, message } }
    function failure(error) { return { type: messageConstants.CREATE_FAILURE, error } }
}

// prefixed function name with underscore because delete is a reserved word in javascript
function _delete(id) {
    return dispatch => {
        dispatch(request(id));

        messageService.delete(id)
            .then(
                message => dispatch(success(id)),
                error => dispatch(failure(id, error.toString()))
            );
    };

    function request(id) { return { type: messageConstants.DELETE_REQUEST, id } }
    function success(id) { return { type: messageConstants.DELETE_SUCCESS, id } }
    function failure(id, error) { return { type: messageConstants.DELETE_FAILURE, id, error } }
}


