import config from 'config';
import { authHeader } from '../helpers';
import { userService } from './user.service'

export const messageService = {
    getAll,
    getByUsername,
    getMessageGroup,
    getPolicies,
    getComplaints,
    postMessageData,
    delete: _delete
};

function getAll() {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };

    // return fetch(`${config.apiUrl}/message`, requestOptions).then(handleResponse);
    return fetch(`${config.apiUrl}/message/list`, requestOptions).then(handleResponse);
}
function getPolicies() {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };
    return fetch(`${config.apiUrl}/policy`, requestOptions).then(handleResponse);
}

function getComplaints() {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };
    return fetch(`${config.apiUrl}/complaint`, requestOptions).then(handleResponse);
}

function getByUsername(username) {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };

    return fetch(`${config.apiUrl}/message/user/${username}`, requestOptions).then(handleResponse);
}

function getMessageGroup(username,group) {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };

    // return fetch(`${config.apiUrl}/message/user-group/${username}/${group}`, requestOptions).then(handleResponse);
    // get all messages from groupId
    return fetch(`${config.apiUrl}/message/group/${group}`, requestOptions).then(handleResponse);
}

function postMessageData(payload) {
    const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload)
    };

    return fetch(`${config.apiUrl}/message`, requestOptions)
        .then(handleResponse);
}

// prefixed function name with underscore because delete is a reserved word in javascript
function _delete(id) {
    const requestOptions = {
        method: 'DELETE',
        headers: authHeader()
    };

    return fetch(`${config.apiUrl}/message/${username}`, requestOptions).then(handleResponse);
}

function handleResponse(response) {
    return response.text().then(text => {
        const data = text && JSON.parse(text);
        if (!response.ok) {
            if (response.status === 401) {
                // auto logout if 401 response returned from api
                //userService.logout();
                //location.reload(true);
            }

            const error = (data && data.message) || response.statusText;
            return Promise.reject(error);
        }

        return data;
    });
}


