import PropTypes from 'prop-types'
import React, { Component } from 'react'
import {
  Grid,
  Responsive,
  Segment,
  Button
} from 'semantic-ui-react'
import 'styling/semantic.less'
import { connect } from 'react-redux'

import { history } from '../../helpers';
import { userActions } from '../../actions'
import ComplaintsHeader from '../ComplaintsHeader'

class CommunicationLayout extends Component {
  constructor(props) {
    super(props);
    this.props.dispatch(userActions.currentUser());
    this.state = {}
  }
  render() {
    const { children } = this.props
    return (
    // <Responsive minWidth={Responsive.onlyTablet.minWidth}>
    <Responsive>
    <ComplaintsHeader user={this.props.user}/>
    
    <Segment
    //style={{ padding: '3em 0em' }}
    vertical>
      <Grid container stackable verticalAlign='top'>
        <Grid.Row>
          <Grid.Column floated='left' width={12}>
            {children}
          </Grid.Column>
        </Grid.Row>
      </Grid>
    </Segment>
  </Responsive>
    )
  }
}

CommunicationLayout.propTypes = {
  children: PropTypes.node,
}

//maps store saved state to pros
const mapStateToProps = (state) => {
	return{
    ...state.users.loading,
		...state.users
	}
}
export default connect(mapStateToProps)(CommunicationLayout)


