import PropTypes from 'prop-types'
import React, { Component } from 'react'
import {
  Grid,
  Responsive,
  Segment,
  Button,
  Image, Form, Input
} from 'semantic-ui-react'
import 'styling/semantic.less'

import { history } from '../../helpers';

class MailChainOne extends Component {
  goToNext() {
    let pathname = location.pathname;
    if(pathname =='/mail') {
      history.push('/inbox-view')
    } else if(pathname == '/mail-new'){
      history.push('/mail-home')
    } else {
      history.push('/mail-cix')
    }
  }
  render() {
    const { children } = this.props
    let compose = '/images/compose.png'
    let textBoxStyle = {
      position: 'absolute',
      top: '178px',
      left: '5px',
      width: '84%',
      fontSize: '12px',
      height: '320px'
    }
    let toStyle = {
      position: 'absolute',
      left: '87px',
      top: '115px',
      height: '20px',
      fontSize: '12px',
      width: '77%'
    }
    let ccStyle = {
      position: 'absolute',
      left: '87px',
      top: '137px',
      height: '20px',
      fontSize: '12px',
      width: '77%'
    }
    let subjectStyle = {
      position: 'absolute',
      left: '87px',
      top: '159px',
      height: '20px',
      fontSize: '12px',
      width: '77%'
    }
    let btnStyle ={
      background: 'none',
      position: 'absolute',
      left: '0',
      top: '109px',
      height: '59px'
    }

    return (
      <Segment
        textAlign='center'
        style={{ padding: '0em'}}
        vertical
        >
      <Image src={compose} verticalAlign='middle' style={{position: 'absolute',left: 0}}/>
      <Form onSubmit={this.handleSubmit}>
            {/* <Form.Field>
              <label>To</label>
              <Input placeholder='To Address' name='to' onChange={this.handleChange}/>
            </Form.Field>
            <Form.Field>
              <label>Subject</label>
              <Input placeholder='Subject' name='subject' onChange={this.handleChange} />
            </Form.Field> */}
            <Input placeholder='' name='to' onChange={this.handleChange} style={toStyle}/>
            <Input placeholder='' name='cc' onChange={this.handleChange} style={ccStyle}/>
            <Input placeholder='' name='cc' onChange={this.handleChange} style={subjectStyle}/>
            <Form.TextArea name='message' onChange={this.handleChange} style={textBoxStyle} />
            <Button style={btnStyle} onClick={()=>this.goToNext()}></Button>
            {/* <Form.Button floated='right' color='blue'>Send</Form.Button> */}
          </Form>
      </Segment>
    )
  }
}

MailChainOne.propTypes = {
  children: PropTypes.node,
}

export default MailChainOne


