import PropTypes from 'prop-types'
import React, { Component } from 'react'
import {
  Grid,
  Responsive,
  Segment,
  Button,
  Image, Form, Input
} from 'semantic-ui-react'
import 'styling/semantic.less'

import { history } from '../../helpers';

class InboxView extends Component {
  goToNext() {
    history.push('/mail-view')
  }
  render() {
    const { children } = this.props
    let compose = '/images/inbox.png'
    let btnStyle ={
      background: 'none',
      position: 'absolute',
      left: '120px',
      top: '165px',
      width: '167px'
    }

    let msgValue = '<p>tttt</p><br/>thanks'
    let toValue = ''
    let subjectValue = 'Hello tes st  '

    return (
      <Segment
        textAlign='left'
        style={{ padding: '0em'}}
        vertical
        >
      <Image src={compose} verticalAlign='middle' style={{position: 'absolute',left: 0}}/>
      <Form onSubmit={this.handleSubmit}>
            {/* <Form.Field>
              <label>To</label>
              <Input placeholder='To Address' name='to' onChange={this.handleChange}/>
            </Form.Field>
            <Form.Field>
              <label>Subject</label>
              <Input placeholder='Subject' name='subject' onChange={this.handleChange} />
            </Form.Field> */}
            <Button style={btnStyle} onClick={()=>this.goToNext()}></Button>
            {/* <Form.Button floated='right' color='blue'>Send</Form.Button> */}
          </Form>
      </Segment>
    )
  }
}

InboxView.propTypes = {
  children: PropTypes.node,
}

export default InboxView


